
import {defineComponent} from 'vue';

export default defineComponent({
    name:'CronometroTempo',
    props:{
        tempoEmSegundos:{
            type: Number,
            default: 0
        }
    },
    computed:{
        tempoDecorrido() : string{
            return new Date(this.tempoEmSegundos *1000).toISOString().slice(11,19)
        }
    },
})
